<template lang="pug">
.chapter
  SimControls(
    :handsOff="handsOff"

    , :paused.sync="paused"
    , :playRate.sync="playRate"
    , :graphOpen.sync="graphOpen"
    , :cameraTarget.sync="cameraTarget"
    , :cameraFollow.sync="cameraFollow"
    , :solarDaysPerYear.sync="solarDaysPerYear"
    , :eccentricity.sync="eccentricity"
    , :tiltAngle.sync="tiltAngle"

    , :showGrid.sync="showGrid"
    , :showEarthOrbits.sync="showEarthOrbits"
    , :showSunOrbits.sync="showSunOrbits"

    , :showSun.sync="showSun"
    , :showMeanSun.sync="showMeanSun"
    , :showEOTWedge.sync="showEOTWedge"
  )
    .eot-graph(v-if="graphOpen")
      EOTGraph(:eccentricity="eccentricity", :tilt="tiltAngle * deg", :mean-anomaly="meanAnomaly")

  transition(name="slide-left", appear)
    JasperTV.tv(
        v-if="copilotState.showTv"
      , :screen-on="copilotState.tvOn"
      , :pose="copilotState.tvPose"
      , :img="copilotState.tvImg"
    )
  DaySim(
    ref="sim"
    , :viewWidth="viewWidth"
    , :viewHeight="viewHeight"
    , :playerLoading="playerLoading"
    , :showGrid="showGrid"
    , :cameraTarget="cameraTarget"
    , :cameraFollow="cameraFollow"
    , :showEarthOrbits="showEarthOrbits"
    , :showSunOrbits="showSunOrbits"
    , :showEOTWedge="showEOTWedge"
    , :showSun="showSun"
    , :showMeanSun="showMeanSun"
    , :showMonthLabels="showMeanSun"
    , :showSiderialDayArc="showSiderialDayArc"
    , :showMeanDayArc="showMeanDayArc"
    , :showSolarDayArc="showSolarDayArc"
    , :showPM="showPM"

    , :tiltAngle="tiltAngle * deg"
    , :eccentricity="eccentricity"
    , :days="day"
    , :daysPerYear="daysPerYear"

    , :enableDragging="enableDragging"
    , @dragstart="dragStart"
    , @drag="drag"
    , @dragend="dragEnd"
    , @camera:start="cameraDragging = true"
    , @camera:end="cameraDragging = false; meddleCamera()"
    , @camera:change="meddleCamera"
  )
    .earth-label(slot="earth-label")
      transition(name="fade")
        label.angle-label(v-if="showDegrees") {{dayAngle.toFixed(0)}}
          span.degrees &deg;
    .mean-label(slot="mean-label", :class="{ down: solarLabelAbove, up: !solarLabelAbove }")
      transition(name="fade")
        .clock(v-if="showMeanClock")
          .time {{meanClock}}
    .solar-label(slot="solar-label", :class="{ up: solarLabelAbove, down: !solarLabelAbove }")
      transition(name="fade")
        .clock(v-if="showSolarClock")
          .time {{solarClock}}
</template>

<script>
import Copilot from 'copilot'
import * as THREE from 'three'
import DaySim from '@/components/entities/day-sim'
import EOTGraph from '@/components/entities/eot-graph'
import SimControls from '@/components/sim-controls'
import JasperTV from '@/components/jasper-tv'
import chapterMixin from '@/components/chapters/chapter.mixin'
import { PERHELION, VERNAL, calcEOT } from '@/lib/stellar-mechanics'

const Pi2 = Math.PI * 2

export default {
  name: 'reality'
  , mixins: [ chapterMixin ]
  , components: {
    DaySim
    , EOTGraph
    , SimControls
    , JasperTV
  }
  , data: () => ({
  })
  , methods: {
    initCopilot(){
      const solarDaysPerYear = 8

      // function solarDaysToOrbitalPos( days, e = 0, y = 0 ){
      //   const dpy = solarDaysPerYear + 1
      //   let eot = 0
      //   if ( e || y ){
      //     eot = calcEOT( days/solarDaysPerYear * Pi2 - PERHELION, e, y, PERHELION - VERNAL )
      //   }
      //   return (days - PERHELION/Pi2 - eot/Pi2) / (dpy - 1)
      // }

      // copilot
      let frames = this.frames = Copilot({
        handsOff: {
          type: Boolean
          , default: false
          , interpolatorOpts: { threshold: 0 }
        }
        , orbitalPosition: {
          type: Number
          , default: 0 // {0, 1}
          , interpolatorOpts: { modulo: 1 }
        }
        , solarDaysPerYear: solarDaysPerYear
        , tiltAngle: 0
        , eccentricity: 0
        , cameraTarget: {
          type: String
          , default: 'meanSun'
          , interpolator: Copilot.Interpolators.Step
          , interpolatorOpts: { threshold: 0 }
        }
        , cameraFollow: false
        , showEarthOrbits: true
        , showSunOrbits: false
        , showEOTWedge: false
        , showSun: true
        , showMeanSun: true
        , showMonthLabels: true
        , showSiderialDayArc: false
        , showMeanDayArc: true
        , showSolarDayArc: true
        , showPM: true
        , showGrid: false

        , showStellarClock: true
        , showSolarClock: true
        , showMeanClock: true
        , showDegrees: false

        , cameraPosition: {
          type: 'Vector3'
          , default: new THREE.Vector3(0, 20, 20)
        }
        , cameraRotation: {
          type: 'Vector3'
          , default: new THREE.Vector3(0, 0, 0)
        }
        , cameraZoom: 20
        , showTv: false
        , tvOn: false
        , tvPose: {
          type: String
          , default: 'greeting'
          , interpolator: Copilot.Interpolators.Step
          , interpolatorOpts: { threshold: 0 }
        }
        , tvImg: {
          type: String
          , default: ''
          , interpolator: Copilot.Interpolators.Step
          , interpolatorOpts: { threshold: 0 }
        }
      }, {
        defaultTransitionDuration: '3s'
      })

      frames.add({
        showSolarClock: false
        , showMeanClock: false
      }, {
        time: 1
        , duration: 1
      })

      frames.add({
        orbitalPosition: 2 + 356 / 365
      }, {
        time: '00:40'
        , startTime: 1
      })

      frames.add({
        eccentricity: 0.0167
        , showEOTWedge: true
      }, {
        time: '00:18'
        , duration: '1s'
        , easing: Copilot.Easing.Quadratic.InOut
      })

      frames.add({
        tiltAngle: 23.439
      }, {
        time: '00:21'
        , duration: '1s'
        , easing: Copilot.Easing.Quadratic.InOut
      })

      this.setQueue('00:26', () => {
        this.graphOpen = true
      })

      frames.add({
        showTv: true
      }, {
        time: '00:27'
        , duration: 1
      })

      frames.add({
        tvOn: true
        , tvImg: 'https://upload.wikimedia.org/wikipedia/commons/thumb/7/7a/Equation_of_time.svg/2560px-Equation_of_time.svg.png'
      }, {
        time: '27.5s'
        , duration: 1
      })

      frames.add({
        tvOn: false
      }, {
        time: '00:33'
        , duration: 1
      })

      frames.add({
        showTv: false
        , tvImg: ''
      }, {
        time: '00:34'
        , duration: 1
      })

      frames.add({
        orbitalPosition: 3 + 356 / 365
      }, {
        time: '01:08'
        , startTime: '00:42'
      })

      frames.add({
        cameraPosition: new THREE.Vector3(20, 30, 0.1)
        , cameraFollow: true
        , cameraZoom: 30
      }, {
        time: '00:43'
        , duration: '2s'
        , easing: Copilot.Easing.Quadratic.InOut
      })

      frames.add({
        orbitalPosition: 3 + 259 / 365
      }, {
        time: '01:15'
        , duration: '4s'
        , easing: Copilot.Easing.Quadratic.InOut
      })

      frames.add({
        cameraFollow: false
      }, {
        time: '01:21'
        , duration: 1
      })

      frames.add({
        cameraPosition: new THREE.Vector3(0.1, 30, 20)
        , cameraZoom: 20
      }, {
        time: '01:22'
        , duration: '1s'
        , easing: Copilot.Easing.Quadratic.InOut
      })

      frames.add({
        orbitalPosition: 5
      }, {
        time: '02:00'
        , startTime: '01:20'
      })

      this.setQueue('02:00', () => {
        this.graphOpen = false
      })

      frames.add({
        cameraFollow: true
      }, {
        time: '02:03'
        , duration: 1
      })

      frames.add({
        cameraPosition: new THREE.Vector3(20, 0.1, 0.1)
        , cameraZoom: 40
        , showEarthOrbits: false
        , showEOTWedge: false
        , daysPerYear: 0
      }, {
        time: '02:07'
        , duration: '2s'
        , easing: Copilot.Easing.Quadratic.InOut
      })

      frames.add({
        orbitalPosition: 8
      }, {
        time: '02:39'
        , startTime: '02:12'
      })

      frames.add({
        showTv: true
      }, {
        time: '02:23'
        , duration: 1
      })

      frames.add({
        tvOn: true
        , tvImg: 'https://www.tierraadentro.cultura.gob.mx/wp-content/uploads/2015/06/tumblr_lwtlvdrLo91r8lm4no1_500.jpg'
      }, {
        time: '02:24'
        , duration: 1
      })

      frames.add({
        tvOn: false
      }, {
        time: '02:34'
        , duration: 1
      })

      frames.add({
        showTv: false
        , tvImg: ''
      }, {
        time: '02:35'
        , duration: 1
      })

      frames.add({
        eccentricity: 0.0934
        , tiltAngle: 25.19
      },{
        time: '02:42'
        , duration: '1s'
      })

      frames.add({
        orbitalPosition: 10
      }, {
        time: '03:00'
        , startTime: '02:42'
      })

      // mars tv
      frames.add({
        showTv: true
      }, {
        time: '02:47'
        , duration: 1
      })

      frames.add({
        tvOn: true
        , tvImg: 'data:image/jpeg;base64,'
      }, {
        time: '02:48'
        , duration: 1
      })

      frames.add({
        tvOn: false
      }, {
        time: '02:55'
        , duration: 1
      })

      frames.add({
        showTv: false
        , tvImg: ''
      }, {
        time: '02:56'
        , duration: 1
      })

      frames.add({
        cameraPosition: new THREE.Vector3(0, 30, 20)
        , cameraFollow: false
        , cameraZoom: 30
        , showEarthOrbits: true
        , showEOTWedge: true
        , daysPerYear: solarDaysPerYear
        , showSolarClock: true
        , showMeanClock: true
      }, {
        time: '03:02'
        , duration: '2s'
        , easing: Copilot.Easing.Quadratic.InOut
      })

      // last frame
      frames.add({
        handsOff: true
      }, {
        time: '03:45'
        , startTime: '03:02'
      })

      this.setQueue('03:02', () => {
        this.paused = false
      })
    }
  }
}
</script>

<style scoped lang="sass">
.tv
  position: absolute
  right: 4px
  bottom: 4px
  width: 300px
  height: 9/16 * 300px
  z-index: 1

@media screen and (max-width: 480px)
  .tv
    width: 140px
    height: 9/16 * 140px
.chapter
  background: $background
  cursor: crosshair
  overflow: hidden

.tools
  position: absolute
  z-index: 1
  left: 3em
  bottom: -1em
  pointer-events: all

  .icon-btn
    cursor: pointer
    transition: color 0.15s ease
    color: $red
    &:hover,
    &:active
      color: lighten($red, 10)

.earth-label,
.stellar-label,
.solar-label,
.mean-label
  text-shadow: 0 0 3px rgba(0, 0, 0, 1)
  font-family: $family-monospace

  transition: bottom 0.3s ease
  &.up
    position: relative
    bottom: 0.85em
  &.down
    position: relative
    bottom: -0.85em
.earth-label
  position: relative
  bottom: 1em
.stellar-label
  color: $blue
.solar-label
  color: $yellow
.mean-label
  color: $red
.clock
  position: absolute
  margin-top: -1rem
  right: 0
  font-family: $family-monospace
  text-align: center
.angle-label
  display: inline-block
  width: 2.5em
  text-align: center
.degrees
  letter-spacing: -1em

.slide-left-enter,
.slide-left-leave-to
  transform: translate(300px, 0)
.slide-left-enter-active,
.slide-left-leave-active
  transition: all 0.5s ease
.slide-left-enter-to,
.slide-left-leave
  transform: translate(0, 0)

.fade-enter-active,
.fade-leave-active
  transition: opacity 0.3s ease
.fade-enter,
.fade-leave-to
  opacity: 0
</style>
